.xlog-profiler {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.5);
    background-color: white;
    color: #333;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
    z-index: 5;
    border-radius: 3px;
    font-size: 8px;
    border: 1px solid rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 0 0 10px #CCC;
            box-shadow: 0 0 10px #CCC;

}

.xlog-profiler .close-btn {
    display: inline-block;
    width: 12px;
    height: 12px;
    cursor: pointer;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
    float: right;
    margin-top: 4px;
    margin-right: 4px;
}

.xlog-profiler .close-btn:before,
.xlog-profiler .close-btn:after {
    content: '';
    position: absolute;
    height: 1px;
    width: 90%;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: #666;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.xlog-profiler .close-btn:hover:before,
.xlog-profiler .close-btn:hover:after {
    background: #000;
}

.xlog-profiler .close-btn:before {
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
}

.xlog-profiler .close-btn:after {
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.xlog-profiler .profile-list {
    width: 50%;
    height: 100%;
    overflow-y: scroll;
    padding: 10px;
    border-right: 2px solid #CCC;
    -webkit-transition: width 0.3s;
    -o-transition: width 0.3s;
    transition: width 0.3s;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.xlog-profiler.select-row .profile-list {
    width: 20%;
    -webkit-transition: width 0.3s;
    -o-transition: width 0.3s;
    transition: width 0.3s;
}

.xlog-profiler .profile-steps {
    width: 50%;
    height: 100%;
    padding: 0;
    -webkit-transition: width 0.3s;
    -o-transition: width 0.3s;
    transition: width 0.3s;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    transition: width 0.3s;
    text-align: left;
}

.xlog-profiler .profile-steps-control {
    height: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-bottom : 1px solid #CCC;
}

.xlog-profiler .profile-steps-control .profile-control-btn {
    display: inline-block;
    height: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 4px 6px 0;
    border-right: 1px solid #CCC;
    cursor: pointer;
}

.xlog-profiler .profile-steps-control .profile-control-btn:hover {
    background-color: #AFE4FD;
}

.xlog-profiler .profile-steps-control .profile-control-btn.active {
    background-color: #1464F4;
    color: white;
}

.xlog-profiler .profile-steps-content {
    overflow-y: auto;
    height: calc(100% - 20px);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.xlog-profiler .profile-steps.select-row {
    width: 80%;
    -webkit-transition: width 0.3s;
    -o-transition: width 0.3s;
    transition: width 0.3s;
}


