html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    margin: 0;
    padding: 0;
}

html {
    background-color: #EEE;
    background: #207ce5;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Bungee', 'NanumBarunGothic', sans-serif;
    background-color: #EEE;
    background: #207ce5;
    _background: #207ce5;
    _background: -webkit-linear-gradient(top, #207ce5 0%, #499bea 100%);
    _background: -webkit-gradient(linear, left top, left bottom, from(#207ce5), to(#499bea));
    _background: -o-linear-gradient(top, #207ce5 0%, #499bea 100%);
    _background: linear-gradient(to bottom, #207ce5 0%, #499bea 100%);
    _filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#207ce5', endColorstr='#499bea', GradientType=0);
}

.btn {
    border: none;
    _background-color: #c6463d;
    _background-color: #3B6AA0;
    _color: white;
    padding: 5px 10px 7px;
    margin-left: 5px;
    font-size: 12px;
    border-radius: 2px;
    display: inline-block;
    outline: none;
    cursor: pointer;
    background-color: white;
    border : 1px solid #BBB;
}

.btn:hover {
    _background-color: #26466D;
    font-weight: bold;
}

.noselect {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
}


.scrollbar::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}

.scrollbar::-webkit-scrollbar-track {
    -webkit-box-shadow: 0 0 6px rgba(0,0,0,0.4);
}

.scrollbar::-webkit-scrollbar-thumb {
    background-color: rgba(0,0,0,0.4);
}


body::-webkit-scrollbar {
    width: 7px;
    height: 7px;
}

body::-webkit-scrollbar-track {
    -webkit-box-shadow: 0 0 6px rgba(0,0,0,0.4);
}

body::-webkit-scrollbar-thumb {
    background-color: rgba(0,0,0,0.4);
    background-color: #FCDC3B;
}


